import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
      srcs:null,
  },
  mutations: {
      add(state,srcc){
          state.srcs=srcc+1;
      },
      jia(state){
          if(state.srcs==1){
              state.srcs = 24;
          }
          else {
              state.srcs-=1;
          }
          console.log("right")
      },
      jian(state){
          if(state.srcs==24){
              state.srcs = 1;
          }
          else {
              state.srcs+=1;
          }
          console.log("left")
      }
  },
  actions: {

  }
})
